@import url("https://fonts.googleapis.com/css?family=PT+Mono");
.contain_seven {
  width: calc(100vw - 20px);
  width: 100vw;
  padding: 0 5px; }

.the_day {
  position: absolute;
  left: 10px; }

.no_overflow {
  overflow: hidden; }

.close_log_form {
  width: calc(100% - 20px);
  margin-left: 10px;
  margin-top: 0;
  -webkit-animation: slide_up .84s;
          animation: slide_up .84s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  border-bottom: 5px solid #556270;
  padding-bottom: 5px; }

.log_day {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 50px;
  border-radius: 5px;
  background: white;
  padding: 0 10px;
  color: #556270;
  font-size: 22px;
  font-weight: bolder;
  margin-top: 5px;
  width: calc(100% - 10px);
  margin-left: 5px; }

@-webkit-keyframes slide_down {
  to {
    margin-top: 0; } }

@keyframes slide_down {
  to {
    margin-top: 0; } }

@-webkit-keyframes slide_up {
  to {
    margin-top: -400px; } }

@keyframes slide_up {
  to {
    margin-top: -400px; } }

@media only screen and (min-width: 1024px) {
